.background,
.gradientbg-icon {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}
.background {
  background-color: #313030;
}
.gradientbg-icon {
  max-width: 100%;
  max-height: 100%;
}
.setlogo-icon {
  position: absolute;
  top: 75px;
  left: calc(50% - 667.5px);
  width: 1336px;
  height: 888px;
  object-fit: cover;
}
.blurbg,
.navpanel {
  position: absolute;
  left: 0;
}
.blurbg {
  top: 73px;
  background-color: rgba(217, 217, 217, 0.33);
  backdrop-filter: blur(27px);
  width: 1454px;
  height: 951px;
  display: none;
}
.navpanel {
  width: 100%;
  top: 0;
  right: 0;
  background-color: #2f2f2f;
  height: 73px;
}
.about {
  position: relative;
  font-size: var(--font-size-5xl);
  font-family: var(--font-inter);
  color: var(--color-white);
  text-align: center;
}
.about-wrapper {
  cursor: pointer;
  border: 0;
  padding: var(--padding-3xs);
  background-color: transparent;
  position: absolute;
  top: 0;
  right: 733px;
  width: 180px;
  height: 73px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.arts,
.cuisine {
  position: relative;
}
.arts {
  z-index: 0;
}
.cuisine {
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.75);
}
.concept-wrapper,
.cuisine-wrapper,
.nft-wrapper,
.portrait-wrapper {
  position: absolute;
  top: 219px;
  right: 0;
  width: 180px;
  height: 73px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs);
  box-sizing: border-box;
}
.concept-wrapper,
.nft-wrapper,
.portrait-wrapper {
  top: 0;
}
.concept-wrapper,
.portrait-wrapper {
  top: 73px;
}
.portrait-wrapper {
  top: 146px;
}
.frame-parent {
  margin: 0 !important;
  top: 73px;
  left: 0;
  height: 291px;
  display: none;
  z-index: 1;
}
.arts-parent,
.frame-group,
.frame-parent {
  position: absolute;
  width: 180px;
}
.arts-parent {
  top: 0;
  right: 368px;
  height: 73px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs);
  box-sizing: border-box;
  gap: var(--gap-3xs);
}
.arts-parent:hover .frame-parent,
.science-parent:hover .frame-group {
  display: flex;
}
.frame-group {
  margin: 0 !important;
  top: 73px;
  left: 0;
  height: 146px;
  display: none;
  z-index: 1;
}
.science-parent {
  position: absolute;
  top: 0;
  right: 1px;
  width: 180px;
  height: 73px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs);
  box-sizing: border-box;
  gap: var(--gap-3xs);
}
.desktop2 {
  position: relative;
  width: 100%;
  height: 1024px;
  overflow: hidden;
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-inter);
}
